import React from "react";

const Loading = () => {
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center loading-background">
      <div className="relative">
        <div
          className="spinner-border animate-spin inline-block w-16 h-16 border-4 rounded-full border-t-orange-500"
          role="status"
        >
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    </div>
  );
};

export default Loading;